// @/components/explanation-section.tsx
interface Explanation {
  answer?: string;
  error?: string;
}

// Zmodyfikowano typ props√≥w, aby formalnie obs≈Çu≈ºyƒá, ≈ºe explanation mo≈ºe byƒá opcjonalne (undefined).
export function ExplanationSection({ explanation }: { explanation?: Explanation }) {
  
  // POPRAWKA: Dodano sprawdzenie, czy 'explanation' istnieje, zanim odczytamy 'error'.
  if (explanation && explanation.error) {
    return (
      <div className="p-4 bg-destructive/10 text-destructive rounded-lg border">
        ‚ùå <strong>B≈ÇƒÖd:</strong> {explanation.error}
      </div>
    );
  }

  // Dodatkowo, je≈õli 'explanation' w og√≥le nie zosta≈Ço przekazane lub jest puste,
  // mo≈ºemy wy≈õwietliƒá stan oczekiwania, zanim przejdziemy do dalszego renderowania.
  if (!explanation || !explanation.answer) {
    return (
        <div className="rounded-lg border bg-card p-6">
            <p className="text-muted-foreground">Oczekiwanie na odpowied≈∫‚Ä¶</p>
        </div>
    );
  }

  // ‚úÖ Renderujemy surowƒÖ odpowied≈∫ AI
  return (
    <div className="rounded-lg border bg-card p-6 prose max-w-none">
      <h3 className="font-bold text-lg mb-4">üß† Pe≈Çne wyja≈õnienie:</h3>
      {/* Skoro doszli≈õmy tutaj, 'explanation' i 'explanation.answer' istniejƒÖ */}
      <div
        className="text-muted-foreground leading-relaxed"
        dangerouslySetInnerHTML={{
          __html: explanation.answer
            .replace(/\n{2,}/g, '</p><p>')
            .replace(/\n/g, '<br/>')
        }}
      />
    </div>
  );
}